import { forwardRef, useState } from "react";
import {
    Button,
    Card,
    Container,
    Group,
    MultiSelect,
    NumberInput,
    Paper,
    SelectItem,
    Text,
    Title,
} from "@mantine/core";
import type {
    MantineSize,
    MantineNumberSize,
} from "@mantine/core"

import { Asset, Balance } from "~/moralis.server";

type Dict<T> = {
    [key: string]: T
};

type PayProps = {
    label: string,
    value: string
};

const PayerSelect = (props: any) => {
    const { title, balances } = props;
    const balanceMap: Dict<Balance> = {};
    const items: SelectItem[] = balances.map((b: Balance) => {
        balanceMap[b.token_address] = b;
        return {
            label: `${b.name} (${b.symbol})`,
            value: b.token_address
        }
    });

    const [value, setValue] = useState<string[]>([])

    // console.log(value);
    console.log(balances);

    const PayItem = forwardRef<HTMLDivElement, PayProps>(
        ({ label, value, ...others }: PayProps, ref) => {
            return (
                <div ref={ref} style={{ width: "100%" }} {...others}>
                    {/* <SimpleGrid cols={1}>
                        <Button variant="outline" m="xs"> */}
                    <span>
                        <Text size="md" color="bold" component="span">
                            {`${balanceMap[value].name}`}
                        </Text>
                        <Text size="xs" color="dimmed" component="span">
                            {` (${balanceMap[value].symbol})`}
                        </Text>
                    </span>
                    {/* <Text size="md">{balanceMap[value].name}</Text> */}
                    {/* </Button>
                    </SimpleGrid> */}
                </div>
            );
        }
    );

    return (
        <Paper withBorder p="xl" radius="md" mt="lg">
            <Title order={4} align="center">{title}</Title>
            {value.map((v: string, i: number) => {
                const balance = balanceMap[v];
                return (
                    <Card radius="md" mt="xs">
                        <NumberInput
                            // variant="unstyled"
                            defaultValue={0.00}
                            precision={2}
                            size="xl"
                            rightSection={<Text component="span" mr="xl">{balance.symbol}</Text>}
                        />
                        <Group mt="xs">
                            <Text component="span">Balance:</Text>
                            <Text component="span" >{balance.balance.toLocaleString()}</Text>
                            <Text component="span" >{balance.symbol}</Text>
                        </Group>
                    </Card>
                )
            })}
            <Text color="dimmed" mt="lg">Select tokens to deposit:</Text>
            <MultiSelect
                data={items}
                // label="Select tokens to deposit:"
                itemComponent={PayItem}
                valueComponent={() => null}
                value={value}
                onChange={setValue}
                mt="xs"
                size="md"
                // placeholder={placeholder}
                searchable
                nothingFound="Nothing found"
                clearable
                clearButtonLabel="Clear selected tokens"
            // filter={(value, selected, item) =>
            //     !selected &&
            //     (item.label?.toLowerCase().includes(value.toLowerCase().trim()) ||
            //         item.description.toLowerCase().includes(value.toLowerCase().trim()))
            // }
            />
        </Paper >
    );
};

