type SwapProps = {
    assets: Asset[]
    balances: Balance[]
};

const Swap = (props: SwapProps) => {
    const { assets, balances } = props;

    return (
        <Container>
            <PayerSelect
                title="Pay Tokens"
                balances={balances}
                placeholder="Select tokens to deposit"
            />
            <ReceiverSelect
                title="Receive Tokens"
                assets={balances}
                placeholder="Select tokens to withdraw"
            />
        </Container>
    );
};

export default Swap;
